<template>

    <div class="order-step-info">

        <div class="order-step-info-title">
            <div class="h2" v-html="$t('create_order.legal_address_title')" />
            <p v-html="$t('create_order.legal_address_descr')" />
        </div>

        <div v-if="currentOrder.orderFullInfo.company.ul || address.length !== 0"
             class="order-person-info-list">

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title"></div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.postcode || ' '}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title"></div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.region || ' '}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title"></div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.city || ' '}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title"></div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.district || ' '}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title"></div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.street || ' '}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title"></div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.home || ' '}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title"></div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.corps || ' '}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title"> / </div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.flat || ' '}}</div>

                </div>
            </div>

        </div>

        <div class="order-person-info-footer-btns">
            <button class="btn btn-secondary"
                    @click.prevent="setOrderStepMinus"></button>
            <button class="btn"
                    :class="{'loading': load}"
                    :disabled="load"
                    @click.prevent="setOrderStepPlus"></button>
        </div>

    </div>

</template>

<script>

    import {mapState, mapGetters} from 'vuex'

    export default {
        name: "CreateOrderStep3",
        data: () => ({
            load: false
        }),
        computed: {
            ...mapState({
                currentOrder: state => state.createOrder,
                order: state => state.createOrder.orderFullInfo,
            }),
            ...mapGetters({
                address: 'address1'
            })
        },
        methods: {
            setOrderStepPlus() {
                this.load = true
                this.$store.dispatch('nextStep').then(() => {
                    this.load = false
                })
            },
            setOrderStepMinus() {
                this.$store.dispatch('prevStep')
            },
            changedVal(bool) {
                this.$emit('canSaveAndNext', bool)
            }
        }
    }
</script>

